body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  color: white;
  background-color: #282c34;
  position: absolute;
  min-height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-width: 100%;
  padding-bottom: 3em;
}

.App-header {
  background-color: #282c34;
  box-shadow: 0px 5px 4px rgba(0,0,0, .3);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: white;
  overflow-wrap: break-word;
  min-height: 11%;
  min-width: 100%;
}

.aboutdiv {
  color:black;
  background-color: #f2c777;
  overflow-wrap: break-word;
  width: 100%;
}

.industry {
  margin: 1em;
  text-align: center;
  padding: 1em 0 1em 0;
}
.industry h4 {
  color: #f2c777;
  margin: 0;
}
.industry h6 {
  margin: 0;
}
.industry:hover {
  cursor: pointer;
}

.industrydiv {
  vertical-align: center;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20em, 1fr));
  grid-gap: 0em;
}

.mia {
  background-color: #282c34;
}

.project {
  margin: 3px;
  padding: 0 1em;
  background-color: #282c34;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  color: white;
  border: 2px solid #f2c777;
}
.project a {
  -webkit-align-items: flex-end;
          align-items: flex-end;
  margin: 0;
  transition: .5s;
}
.project h3 {
  width: 100%;
  color: #f2c777;
  transition: .5s;
  background-color: #282c34;
  -webkit-transform: translateY(3em);
          transform: translateY(3em);
}
.project h4 {
  opacity: 0;
  transition: .1s;
}
.project p {
  opacity: 0;
  transition: .2s;
}
.project:hover {
  border: 2px solid #fff;
  transition: .1s;
}
.project:hover a {
  color: #61dafb;
  transition: .1s;
}
.project:hover h3 {
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  transition: .3s;
}
.project:hover h4 {
  opacity: 1;
  transition: .2s;
}
.project:hover p {
  opacity: 1;
  transition: .2s;
}
.projectdiv {
  display:grid;
  grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
  grid-gap: 1%;
  margin: 1em;
  padding-bottom: 80px;
}

.social {
  margin: 0 0 5px 0;
  display: block;
  -webkit-flex-direction: row;
          flex-direction: row;
  text-align: center;
}
.social button {
  background-color: rgba(0,0,0,0);
  border-color: rgba(0,0,0,0);
}
.social button:hover {
  cursor: pointer;
}

.unused {
  margin: 3px;
  background-color: #f2c777;
  color: black;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border: 2px solid #f2c777;
}

.unused:hover {
  cursor: pointer;
}

.unused h1 {
  -webkit-transform: translateY(-0.5em);
          transform: translateY(-0.5em);
}

.unused a {
  color: black;
}

a {
  color: white;
  text-decoration: none;
  font-style: bold;
}

svg {
  padding: 0 5px 0 5px;
  -webkit-filter: invert(100%);
  filter: invert(100%);
}

